.Skills_section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    /* padding-bottom: 10rem; */
    padding-top: 10rem;

    transform: translateX(-.5rem);

}

.skill_containt {
    width: 80%;
}

.skill_containt .title_section {
    text-align: center;
    margin-bottom: 2rem;

}

.skill_containt .title_section h2 {
    font-size: clamp(1.9rem , 2.63vw, 10vw);
    margin: .1rem;
}

.skill_containt .title_section p {
    font-size: 1.2rem;
    margin: .2rem;
    color: orangered;
}


.skill_containt .details {
    display: flex;
    gap: 3rem;
    padding: 2rem;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.301);
}

.skill_containt .description {
    width: 100%;
}



.skill_containt .description h2 {
    font-size: clamp(1.2rem, 2.2vw, 10vw);
    margin-top: 0;
}

.skill_containt .description p {
    font-size: clamp(1.2rem, 1.4vw, 2vw);
    line-height: 1.8rem;
    color: rgba(0, 0, 0, 0.724);
}

.skill_containt .description button {
    width: 12rem;
    height: 2.5rem;
    font-size: 1.1rem;
    border: 1px solid orangered;
    background-color: rgba(255, 68, 0, 0.099);
    border-radius: .4rem;
    color: orangered;
    font-weight: bold;
    cursor: pointer;
}

.skill_containt .skill_list {
    display: flex;
    flex-direction: column;
    gap: 1.72rem;
    width: 100%;
}

.skill_containt .single_skill .top {
    display: flex;
    justify-content: space-between;
}

.skill_containt .single_skill .top h3 {
    margin: 0;
}

.skill_containt .single_skill h4 {
    margin: 0;
}

.skill_containt .single_skill .bottom {
    position: relative;
    border: 1px solid red;
    height: .3rem;
    margin-top: .4rem;
    border-radius: 1rem;
    background-color: orangered;
}

.skill_containt .single_skill .bottom .bar {
    position: absolute;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: white;
}

.skill_containt .bar1 {
    left: 90%;
}

.skill_containt .bar2 {
    left: 80%;
}

.skill_containt .bar3 {
    left: 70%;
}

.skill_containt .bar4 {
    left: 60%;
}

.skill_containt .bar5 {
    left: 50%;
}



@media (max-width: 1000px) {
    .skill_containt .details {
        flex-direction: column;
    }

    .skill_containt {
        width: 90%;
    }
}