.Contact_section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-bottom: 10rem;
    padding-top: 10rem;

    transform: translateX(-.5rem);

}

.contact_containt {
    width: 80%;

}

.contact_containt .title_section {
    text-align: center;
    margin-bottom: 2rem;

}

.contact_containt .title_section h2 {
    font-size: clamp(1.9rem , 2.63vw, 10vw);
    margin: .1rem;
}

.contact_containt .title_section p {
    font-size: 1.2rem;
    margin: .2rem;
    color: orangered;
}

.contact_containt .details {
    display: flex;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.11);
}

.contact_containt .info {
    width: 100%;
    padding: 1.5rem;
}

.contact_containt .info h3 {
    font-size: 1.8rem;
    margin: .5rem;
    margin-top: 0;
}

.contact_containt .info .desc {
    font-size: 1.2rem;
    line-height: 1.7rem;
    color: rgba(0, 0, 0, 0.681);
}

.contact_containt .single_info {
    display: flex;
    gap: 2rem;
    align-items: center;
    padding: .5rem 0;
}

.contact_containt .single_info i {
    font-size: 1.62rem;
    color: orangered;
}

.contact_containt .single_info h4 {
    margin: .4rem;
    font-size: 1.3rem;
}

.contact_containt .single_info p {
    /* margin: 0; */
    margin: .4rem;
    color: rgba(0, 0, 0, 0.767);
    font-size: 1.1rem;

}



.contact_containt form {
    padding: 1.5rem;
    width: 100%;
    background-color: rgba(128, 128, 128, 0.156);
}

.contact_containt form h2 {
    font-size: 2rem;
    margin: 1rem 0;
    margin-top: 0;
}


.contact_containt form .name_email {
    display: flex;
    gap: 1rem;
}

.contact_containt form input {
    width: 100%;
    height: 2.5rem;
    margin: .51rem 0;
    border-radius: .3rem;
    font-size: 1.1rem;
    border: 1px solid rgba(128, 128, 128, 0.493);
}

.contact_containt form input:focus {
    outline: none;
}

.contact_containt form textarea {
    width: 100%;
    height: 10rem;
    margin-top: 1rem;
    font-size: 1.1rem;
}

.contact_containt form textarea:focus {
    outline: none;
}

.contact_containt form button {
    width: 12rem;
    height: 2.5rem;
    border: 1px solid orangered;
    background-color: orangered;
    color: white;
    font-size: 1.1rem;
    border-radius: .3rem;
    margin-top: 1rem;
    transition: .25s;
    cursor: pointer;
}

.contact_containt form button:hover {
    width: 14rem;
}


@media (max-width: 1150px) {
    .contact_containt .name_email {
        flex-direction: column;
        gap: 0 !important;
    }

    .contact_containt {
        width: 90%;

    }
}

@media (max-width: 900px) {
    .contact_containt {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }


    .contact_containt .details {
        flex-direction: column;
        /* justify-content: center; */
        align-items: center;
        width: 100%;
        gap: 3rem;
        box-shadow: none;
    }

    .contact_containt .info {
        width: 85%;
    }

    .contact_containt .info .desc{
        /* padding: 0; */
        width: 90%
    }

    .contact_containt form {
        width: 80%;
    }

    .contact_containt .name_email {
        flex-direction: row ;
        gap: 1rem !important;
    }

    .contact_containt form input{
        /* padding: 0; */
        /* scale: .99; */
        width: 100%
    };
}


@media (max-width: 501px) {
    .contact_containt .name_email {
        flex-direction: column ;
        gap: 0rem !important;
    }

}