.certificate {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.857);
    z-index: 9999;
    
}

.certificate_box {
    width: 90%;
    height: 90vh;
}

.certificate_box img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.certificate i {
    position: fixed;
    top: 2rem;
    /* right: 3rem; */
    color: white;
    font-size: 2rem;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.344);
    padding: 1rem;
    
}

.bi-x-lg {
   right: 3rem;
}

.bi-download {
    right: 7rem;
}