/* NavBar style */
nav {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    width: 100vw;
    background-color: rgb(31, 30, 30);
    color: white;
}

.navbar_containt {
    display: flex;
    justify-content: space-between;
    width: 90%;
}


/* Logo section, right section in the navbar */
.navbar_containt .logo_section {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar_containt .logo_section h2 {
    margin: .7rem 0;
}

.navbar_containt .logo_section h2 span {
    color: orangered;
}

.navbar_containt .logo_section .links {
    display: none;
}




/* Center section in the navbar */

.navbar_containt ul {
    display: flex;
    align-items: center;
    gap: 2rem;
}

.navbar_containt ul li {
    list-style-type: none;
    cursor: pointer;
}

.navbar_containt ul li:hover a {
    color: rgb(249, 176, 149) ;
}

.navbar_containt li a {
    text-decoration: none;
    /* color: rgb(39, 39, 39); */
    color: white;
    font-size: 1.1rem;
    font-weight: bold;
}




/* Media query, responsivity for screen from 1050px */
@media (max-width: 860px) {
    .navbar_containt {
        flex-direction: column;
    }



    .navbar_containt .logo_section .links {
        display: flex;
        justify-content: space-between;
        gap: 1.52rem;
    }



    .navbar_containt .logo_section .links i {
        font-size: 1.6rem;
        cursor: pointer;
    }

    .navbar_containt ul {
        flex-direction: column;
        padding: 0;
        gap: 0;
    }

    .navbar_containt ul .li {
        /* border: 1px solid red; */
        width: 100%;
    }

    .navbar_containt ul li {
        text-align: center;
        border-bottom: 1px solid rgb(228, 228, 228);
        width: 100%;
        padding: 1rem 0;
        cursor: pointer;
        /* border: 1px solid green; */
    }
    
    /* .navbar_containt ul li a {
        border: 1px solid red;
        width: 100% !important;
        width: 100vw !important;
        padding: 1rem 4rem;
        text-align: center;

    } */

    .navbar_containt ul li:hover {
        /* background-color: rgb(237, 237, 237); */
    }

    nav {
        height: 3.4rem;
        align-items: start;
        overflow: hidden;
    }
    
    nav.active {
        transition: .5s;
        height: 100vh;
    }


}

/* Responsivity for smaller screen */

@media (max-width: 501px) {
    .navbar_containt .links button {
        display: none;
    }

    .navbar_containt .bi-search {
        display: block;
    }

    .navbar_containt .logo_section h2 {
       font-size: 1.3rem;
    }
    
}