.Hero_container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}

html {
    /* scale: 1.05; */
    /* transform: translateX(-.5rem); */
}

.Hero_container .hero_bg {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100vh;
    inset: 0;
    z-index: -1;
    filter: brightness(40%);
}

.hero_containt {
    display: flex;
    /* border: 1px solid rgba(255, 0, 0, 0.225); */
    color: white;
    width: 80%;
    gap: 2rem;
}

.hero_containt .details_section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 60%;
}

.hero_containt h2 {
    font-size: clamp(2rem, 4vw, 10vw);
    margin: .5rem 0;
}

.hero_containt h3 {
    font-size: clamp(1.5rem, 2.4vw, 10vw);
    color: orangered;
    margin: 1rem 0;
}

.hero_containt p {
    font-size: 1.2rem;
    line-height: 1.8rem;
    margin: .4rem 0;
}

.hero_containt .btns_section {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
}

.hero_containt .btns_section button {
    width: 10rem;
    padding: .5rem 0;
    border: 1px solid rgba(255, 255, 255, 0.767);
    border-radius: .3rem;
    background-color: transparent;
    color: white;
    font-size: 1.1rem;
    cursor: pointer;
    transition: .2s;
}

.hero_containt .btns_section .hire_me:hover {
    /* background-color: rgba(255, 68, 0, 0.305); */
    scale: 1.08;
}

.hero_containt .btns_section .download {
    background-color: rgba(255, 68, 0, 0.305);
    
}

.hero_containt .btns_section .download:hover {
    /* background-color: transparent; */
    scale: 1.08;
}

.hero_containt .image_section {
    position: relative;
    height: 30rem;
    width: 40%;
}

.hero_containt .image_section img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: bottom;
}

.hero_containt .image_section .img_border {
     position: absolute;
     width: 15vw;
     height: 15vw;
     background-color: orangered;
     z-index: -1;
     box-shadow: 0 0 1rem rgba(255, 68, 0, 0.569);
    }
    
    .hero_containt .image_section .img_border1 {
        bottom: -.5rem;
        left: -.5rem;
    }

    .hero_containt .image_section .img_border2 {
        top: -.5rem;
        right: -.5rem;
    }


    @media (max-width: 1000px) {
        .hero_containt  {
            flex-direction: column-reverse;
            align-items: center;
            padding-top: 1rem;
            width: 90%;
        }

        .hero_containt .image_section {
            position: relative;
            height: 18rem;
            width: 20rem;
        }
        

        .hero_containt .details_section {
            width: 100%;
            align-items: center;
        }

        .hero_containt .details_section p {
            text-align: center;
        }
    }

    @media (max-width: 501px) {
        .hero_containt .image_section {
            position: relative;
            width: 60vw;
            height: 15rem !important;
            /* border-radius: 10rem !important; */
            /* overflow: hidden; */
        }

        .hero_containt .image_section .img_border {
            width: 25vw;
            height: 25vw;
           }

           .hero_containt h2 {
            font-size: clamp(1.5rem, 8vw, 10vw);
        }
        
        .hero_containt h3 {
            font-size: clamp(1.5rem, 5.4vw, 10vw);
            margin: .61rem 0;
        }
        
        .hero_containt p {
            font-size: 1.1rem;
        }


        
        .hero_containt .btns_section button {
            width: 37vw;
        }


        
    }