
#section7 {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(49, 59, 52);
    color: white;
    padding-top: 50px;
    padding-bottom: 3rem;
}

#section7 .footer_containt {
    display: flex;
    width: 90%;
}

#section7 .link a {
    text-decoration: none;
    color: white;
    padding: 5px 0px;
    font-size: 20px;
}

#section7 .link a:hover {
    color: orangered;
}

#section7 .contact p {
    font-size: 20px;
    margin: 1rem;
}

#section7 .contact p:hover {
    color: orangered;
    cursor: pointer;
}

#section7 .link-box {
    display: flex;
    flex-direction: column;
}


.about-us,
.contact,
.link {
    width: 100%;
    /* padding-left: 80px; */
    /* border: 1px solid red; */
}

.about-us h2,
.contact h2,
.link h2{
    padding-left: 15px;
}

/* #section7 .about-us {
    min-width: 200px;
} */

.about-us .desc {
    line-height: 1.7rem;
    font-size: 1.1rem;
    color: rgba(255, 255, 255, 0.849);
}

#section7 p {
    width: 80%;
    /* color: red; */
}

#section7 h2 {
    border-left: 3px solid orangered;
}

#section7 i {
    font-size: 25px;
    padding: 5px;
    cursor: pointer;
}

#section7 .fa-whatsapp {
    color: rgb(21, 128, 0);
}

#section7 .fa-instagram {
    color: rgb(255, 0, 221);
}

#section7 .fa-facebook {
    color: rgb(0, 81, 255);
}

#section7 .fa-twitter {
    color: rgb(0, 217, 255);
}




@media(max-width: 1000px) {
    .about-us,
    .contact,
    .link {
        padding: 0;
        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        /* align-items: center; */
        margin: 0;
 }

 #section7 .footer_containt {
   flex-direction: column;
}

 .about-us {
    /* margin-left: 5%; */
 }

 #section7 .about-us p {
    width: 100%;
}

#section7 .contact p {
    font-size: 20px;
    margin: .51rem;
}




}




@media (max-width: 801px) {
    #section7 {
        display: flex;
        flex-direction: column;
        padding: 100px 5vw;
        padding-right: 40px;

    }

    #section7 .about-us,
    .contact,
    .link {
        width: 100%;
        padding-bottom: 30px;
        margin-bottom: 10px;
        padding-left: 20px;
        border: 1px solid;

    }

    .about-us {
        margin-left: 0;
     }

    #section7 .about-us p {
        width: 100%;
        /* color: red; */
    }

    #section7 .about-us h2,
    .contact h2,
    .link h2 {
        padding-left: 10px;
    }


    #section7 .contact {
        padding-left: 20px;
    }

    #section7 .link {
        padding-left: 20px;
    }



    #section7 .contact p {
        margin: 5px 0px;
    }

    .about-us,
.contact,
.link {
    padding-left: 0px;
}





}


