.About_section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 10rem;
    /* padding-bottom: 10rem; */
    transform: translateX(-.5rem);

}

.about_containt {
    width: 80%;
}

.about_containt .title_section {
    text-align: center;
    margin-bottom: 3rem;
}

.about_containt .title_section h2 {
    font-size: clamp(1.9rem , 2.63vw, 10vw);
    margin: .1rem;
}

.about_containt .title_section p {
    font-size: 1.2rem;
    margin: .2rem;
    color: orangered;
}

.about_containt .details {
    display: flex;
    gap: 1rem;
}

.about_containt .image_section {
    width: 50%;
    height: 25rem;
}

.about_containt .image_section img {
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: -5rem 0rem;
}

.about_containt .description {
    width: 60%;
    /* border: 1px solid red; */
    padding: 1.5rem;
    box-shadow: 0 0  1rem rgba(0, 0, 0, 0.305);
}

.about_containt .description h3 {
    margin-top: 0;
    font-size: clamp(1.5rem, 1.82vw, 10vw);
} 

.about_containt .description h3 span {
    color: orangered;
}

.about_containt .description p {
    font-size: 1.27rem;
    line-height: 1.8rem;
    color: rgba(0, 0, 0, 0.786);
}

.about_containt .description button {
    width: 12rem;
    padding: .5rem 0;
    margin-top: 1rem;
    border: none;
    background-color: orangered;
    color: white;
    font-size: 1.1rem;
    border-radius: .3rem;
    cursor: pointer;
    transition: .25s;
}

.about_containt .description button:hover {
    width: 13rem;
}


@media (max-width: 1000px) {
    .about_containt {
        width: 80%;
    }


    .about_containt .details {
        flex-direction: column;
        width: 100%;
        /* border: 1px solid red; */
    }

    .about_containt .image_section {
        width: 100%;
        height: 20rem;

    }

    .about_containt .description {
        width: 100%;
        padding: 0;
        box-shadow: none;
        margin-top: 2rem;
    }
}


@media (max-width: 501px) {
    .about_containt .image_section {
        height: 15rem;
    }
}