.services_section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    /* padding-bottom: 10rem; */
    padding-top: 10rem;

    transform: translateX(-.5rem);

}

.services_containt {
    width: 90%;
}

.services_containt .title_section {
    text-align: center;
    margin-bottom: 2rem;
}

.services_containt .title_section h2 {
    font-size: clamp(1.9rem , 2.63vw, 10vw);
    margin: .1rem;
}

.services_containt .title_section p {
    font-size: 1.2rem;
    margin: .2rem;
    color: orangered;
}


.services_containt .services {
    display: grid;
    align-items: center;
    justify-content: center;
    /* border: 1px solid red; */
    gap: 2rem;
    grid-template-columns: repeat(auto-fill, 22rem);
}

.services_containt .single_service {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */
    /* min-width: 20rem; */
    padding: 1.5rem;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.193);
    border-radius: .5rem;
    transition: .2s;
}

.services_containt .single_service:hover {
    background-color: rgba(255, 68, 0, 0.181);
    box-shadow: none;
}





.services_containt .single_service i {
    font-size: 4rem;
    color: orangered;
    transition: .2s;
}

.services_containt .single_service h3 {
    margin: 0;
    font-size: 2rem;

}

.services_containt .single_service p{
    font-size: 1.2rem;
    text-align: center;
    color: rgba(0, 0, 0, 0.649);
    transition: .2s;

}


@media (max-width: 501px) {
    .services_containt .services {
        grid-template-columns: repeat(auto-fill, 80vw);
    }
}